.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
.establishment {
  background-color    : gray;
  padding             : 10px;
  margin              : 5px 30px;
  border-radius       : 8px;
  list-style          : none;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


 .establishment {
  display             : -ms-flexbox;
  display             : flex;
  -ms-flex-align         : center;
      align-items         : center;
  background-color    : gray;
  padding             : 10px;
  margin              : 5px 30px;
  border-radius       : 8px;
}
.establishment-favori {
  -ms-flex : 0.1 1;
      flex : 0.1 1;
}
.establishment-description {
  -ms-flex : 0.7 1;
      flex : 0.7 1;
}
.establishmentLikeDislike {
  -ms-flex            : 0.1 1;
      flex            : 0.1 1;
  display         : -ms-flexbox;
  display         : flex;
  -ms-flex-direction  : column;
      flex-direction  : column;
  -ms-flex-align     : center;
      align-items     : center;
}
.establishmentDelete {
  -ms-flex            : 0.1 1;
      flex            : 0.1 1;
  display         : -ms-flexbox;
  display         : flex;
  -ms-flex-direction  : column;
      flex-direction  : column;
  -ms-flex-align     : center;
      align-items     : center;
}
 .buttonB {
  background  : none;
  border      : none;
  color       : white;
} 
.favoriIcon {
  color: yellow;
}


